<template>
  <div class="add-student-file">
    <v-header :user="userInfo" :createdUser="createdUser"></v-header>
    <div class="add-student clearfix" v-show="studentInfo.name">
      <div class="add-info" >
        <span class="space-left"></span>
        <span>/</span>
        <span>{{studentInfo.name}}</span>
        <span>/</span>
        <span>{{studentInfo.major==1?'':''}}</span>
        <span>/</span>
        <span>{{studentInfo.score}}</span>
        <span>/</span>
        <span>{{studentInfo.precedence}}</span>
      </div>
    </div>
    <div class="file-content">
      <h3 class="title">
        
      </h3>
      <div class="info-form">
        <div class="container">
          <div class="row">
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="Numbering"></label>
                  <input type="number" class="form-control" id="Numbering" disabled v-model="studentInfo.id">
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="ticketNumber"></label>
                  <input type="text" class="form-control" id="ticketNumber" placeholder=""
                         v-model="studentInfo.examNo">
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="idNumber"></label>
                  <input type="text" class="form-control" id="idNumber" placeholder="" v-model="studentInfo.cardNo">
                </div>
              </div>
            </div>
          </div>
          <div class="row m-t-15">
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="studentName"></label>
                  <input type="text" class="form-control" id="studentName" placeholder="" v-model="studentInfo.name">
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="studentName"></label>
                  <div class="radio">
                    <label class="radio-inline">
                       <input type="radio" name="section" value="1" v-model="studentInfo.major">
                    </label>
                    <label class="radio-inline">
                       <input type="radio" name="section" value="0" v-model="studentInfo.major">
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label></label>
                  <div class="radio">
                    <label class="radio-inline">
                       <input type="radio" name="gender" value="0" v-model="studentInfo.sex">
                    </label>
                    <label class="radio-inline">
                       <input type="radio" name="gender" value="1" v-model="studentInfo.sex">
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row m-t-15">
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="schoolName"></label>
                  <input type="text" class="form-control" id="schoolName" placeholder=""
                         v-model="studentInfo.school">
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label></label>
                  <input style="width: 31%;" type="number" class="form-control" placeholder=""
                         v-model="studentInfo.rank">
                  <span>/</span>
                  <input style="width: 31%;" type="number" class="form-control" placeholder=""
                         v-model="studentInfo.stuNum">
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label>/</label>
                  <div class="radio">
                    <label class="radio-inline">
                       <input type="radio" name="fresh" value="0" v-model="studentInfo.stuType">
                    </label>
                    <label class="radio-inline">
                       <input type="radio" name="fresh" value="1" v-model="studentInfo.stuType">
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row m-t-15">
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="score"></label>
                  <input type="number" class="form-control" id="score" placeholder="" v-model="studentInfo.score">
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="ranking"></label>
                  <input type="number" class="form-control" id="ranking" placeholder=""
                         v-model="studentInfo.precedence">
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="form-inline">
                <div class="form-group">
                  <label for="policy"></label>
                  <input type="text" class="form-control" id="policy" placeholder="" v-model="studentInfo.policy">
                </div>
              </div>
            </div>
          </div>
          <div class="row m-t-15">
            <div class="form-inline">
              <div class="form-group">
                <label for="score"></label>
                <span class="subject-name"></span>
                <input type="number" class="form-control subject-score" placeholder="" v-model="studentInfo.chinese">
                <span class="subject-name"></span>
                <input type="number" class="form-control subject-score" placeholder="" v-model="studentInfo.manth">
                <span class="subject-name"></span>
                <input type="number" class="form-control subject-score" placeholder="" v-model="studentInfo.english">
                <span class="subject-name">/</span>
                <input type="number" class="form-control subject-score" placeholder="" v-model="studentInfo.physics">
                <span class="subject-name">/</span>
                <input type="number" class="form-control subject-score" placeholder=""
                       v-model="studentInfo.chemistry">
                <span class="subject-name">/</span>
                <input type="number" class="form-control subject-score" placeholder="" v-model="studentInfo.biology">
                <span class="subject-name"></span>
                <input type="number" class="form-control subject-score" placeholder=""
                       v-model="studentInfo.comprehensive">
              </div>
            </div>
          </div>
          <div class="row m-t-15">
            <div class="form-inline">
              <div class="form-group" style="width: 100%;">
                <label for="restricted"></label>
                <input type="text" class="form-control" style="width: 90%;" id="restricted" placeholder=""
                       v-model="studentInfo.limitContent">
              </div>
            </div>
          </div>
          <div class="row m-t-15">
            <div class="form-inline">
              <div class="form-group" style="width: 100%;">
                <label for="other-info"></label>
                <textarea id="other-info" class="form-control" style="width: 90%;resize: none" rows="3"
                          v-model="studentInfo.other"></textarea>
              </div>
            </div>
          </div>
          <div class="row m-t-15 text-center" style="margin-bottom: 40px;">
            <div class="save-info" @click="saveStuInfo()"></div>
            <div class="make-plan" @click="goMakePlan()"></div>
          </div>
        </div>
      </div>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue';
  import header from '../header/header.vue';
  import footer from '../footer/footer.vue';

  export default {
    name: 'addStudentFile',
    components: {
      'v-header': header,
      'v-footer': footer
    },
    data() {
      return {
        userInfo: '',
        createdUser: '',
        stuNo: '',
        studentInfo: {
          // id: 0,                //Number    
          createdUser: '',                //Number    id
          name: '',                //String    
          examNo: '',                //String    
          cardNo: '',                //String    
          major: '',                //Number    0:1:
          sex: '',                //Number    0:1:
          school: '',                //String    
          rank: '',                //Number    
          stuNum: '',                //Number    
          stuType: '',                //Number    0:1:
          score: '',                //Number    
          precedence: '',                //Number    
          policy: '',                //String    
          limitContent: '',                //String    
          other: '',                //String    
          chinese: '',                //Number    
          english: '',                //Number    
          manth: '',                //Number    
          physics: '',                //Number    /
          chemistry: '',                //Number    /
          biology: '',                //Number    /
          comprehensive: ''                //Number    
        }
      };
    },
    created() {
      this.fetchData();
    },
    methods: {
      fetchData() {
        let username = this.$utils.getUrlKey('username');
        this.userInfo = username;
        this.createdUser = this.$utils.getUrlKey('createdUser');
        if (this.$utils.getUrlKey('stuNo')) { // 
          //id Number    
          this.stuNo = this.$utils.getUrlKey('stuNo');
          this.getStudentDetail();
        }
      },
      /**
       * 
       */
      getStudentDetail() {
        let self = this;
        let params = new URLSearchParams();
        params.append('userId', self.createdUser);
        params.append('stuNo', self.stuNo);
        self.$http.post(this.HOST + 'mendao/getStuByStuNo', params).then((response) => {
          if (response.data.code == 1) {
            let info = response.data.data;
            self.studentInfo = {
              createdUser: info.createdUser,
              name: info.name,
              examNo: info.examNo,
              cardNo: info.cardNo,
              major: info.major,
              sex: info.sex,
              school: info.school,
              rank: info.rank,
              stuNum: info.stuNum,
              stuType: info.stuType,
              score: info.score,
              precedence: info.precedence,
              policy: info.policy,
              limitContent: info.limitContent,
              other: info.other,
              chinese: info.chinese,
              english: info.english,
              manth: info.manth,
              physics: info.physics,
              chemistry: info.chemistry,
              biology: info.biology,
              comprehensive: info.comprehensive
            };
            Vue.set(this.studentInfo, 'id', info.id);
          };
        }, (response) => {
          console.log('');
        });
      },
      /**
       * 
       */
      saveStuInfo() {
        // 
        let self = this;
        self.studentInfo.createdUser = this.createdUser;
        if (self.studentInfo.name == '') {
          alert("");
          return false;
        }
        let params = new URLSearchParams();
        params.append('data', JSON.stringify(self.studentInfo));
        // 
        self.$http.post(this.HOST + 'mendao/editStudent', params).then((response) => {
          if (response.data.code == 1) {
            alert('');
            this.$router.push({
              path: '/studentFileList', query: {
                username: this.userInfo,
                createdUser: this.createdUser,
                stuNo: self.stuNo
              }
            });
          }
        }, (response) => {
        });


      },
      // 
      goMakePlan() {
        let self = this;
        self.$router.push({
          path: '/planSelection', query: {
            username: self.userInfo,
            createdUser: self.createdUser,
            stuNo: self.stuNo
          }
        });
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .add-student-file
    width: 100%
    height: auto
    .add-student
      position: fixed;
      width: 100%;
      height: 37px;
      top: 46px;
      background-color: #fff;
      color: #E65A1E;
      border-bottom: 1px solid #bbbbbb;
      z-index: 2;
      .add-info
        width: 1190px;
        margin: 0 auto;
        height: 36px;
        line-height: 36px;
        font-size: 14px;
      space-left
        margin-left: 63px;

    .file-content
      width: 1200px
      margin: 0 auto
      min-height: 100vh
      padding-top: 83px
      .title
        width: 100%
        height: 49px
        line-height: 49px
        text-align: center
        border: 1px solid rgba(187, 187, 187, 1)
        background-color: rgba(221, 221, 221, 1)
        color: rgba(230, 90, 30, 1)
        font-size: 16px
        font-weight: 600

      .info-form
        width: 80%;
        margin: 0 auto;
        padding-top: 30px;
        .m-t-15
          margin-top: 15px
        .container
          width: 100%
          .form-group
            label
              width: 80px
            .radio
              padding: 5px
              label
                width: auto
                max-width: 100%
            .subject-name
              width: 40px
              display: inline-block
              text-align: center
            .subject-score
              width: 8%
          .save-info
            display: inline-block
            width: 185px;
            height: 45px;
            line-height: 45px;
            border-radius: 19px;
            background-color: rgba(255, 152, 0, 1);
            color: rgba(255, 255, 255, 1);
            font-size: 16px;
            text-align: center;
            box-shadow: 0px 0px 4px 0px rgba(170, 170, 170, 1);
            cursor: pointer
          .make-plan
            display: inline-block
            width: 185px;
            height: 45px;
            margin-left: 20px;
            line-height: 45px;
            border-radius: 19px;
            background-color: rgba(230, 90, 30, 1);
            color: rgba(255, 255, 255, 1);
            font-size: 16px;
            text-align: center;
            box-shadow: 0px 0px 4px 0px rgba(170, 170, 170, 1);
            cursor: pointer


</style>



