import utils from './common/js/utils'

import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import qs from 'qs';
import VueRouter from 'vue-router';

import App from './components/App.vue';
import Login from './components/login/login.vue';
import schoolInfo from './components/school-info/school-info.vue';
import studentFileList from './components/student-file-list/student-file-list';
import addStudentFile from './components/add-student-file/add-student-file';
import studentPlan from './components/student-plan/student-plan';
import planSelection from './components/plan-selection/plan-selection';
// 
Vue.use(Vuex);
// 

Vue.prototype.HOST = 'http://118.31.37.164:8080/';
// Vue.prototype.HOST = 'api/';
Vue.prototype.$utils = utils;
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.options.root = '/';
// axios.headers.common['Authorization'] = 'Basic YXBpOnBhc3N3b3Jk';
axios.options.emulateJSON = true;
Vue.prototype.$http = axios;
Vue.prototype.$qs = qs;
Vue.use(VueRouter);

// 
axios.interceptors.request.use(config => {
  // loading
  return config
}, error => {
  return Promise.reject(error)
});

axios.interceptors.response.use(response => {
  return response
}, error => {
  return Promise.resolve(error.response)
});

// 
// UserMenu.vuerouter-link
const router = new VueRouter({
  routes: [{
    path: '/login',
    component: Login
  }, {
    path: '/schoolInfo',
    component: schoolInfo
  }, {
    path: '/studentFileList',
    component: studentFileList
  }, {
    path: '/addStudentFile',
    component: addStudentFile
  }, {
    path: '/studentPlan',
    component: studentPlan
  }, {
    path: '/planSelection',
    component: planSelection
  },]
});

// Vuex
const store = new Vuex.Store({
  state: {
    domain: 'http://test.example.com', // 
    userInfo: { // 
      nick: null,
      ulevel: null,
      uid: null,
      portrait: null
    }
  },
  mutations: {
    // 
    updateUserInfo(state, newUserInfo) {
      state.userInfo = newUserInfo;
    }
  }
});

// cookie,vue
// vuesessionsessioncookie
// sessionvuex
Vue.prototype.setCookie = (c_name, value, expiredays) => {
  var exdate = new Date();
  exdate.setDate(exdate.getDate() + expiredays);
  document.cookie = c_name + '=' + escape(value) + ((expiredays == null) ? '' : ';expires=' + exdate.toGMTString());
};

// cookie
function getCookie(name) {
  var reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)');
  var arr = document.cookie.match(reg);
  if (arr) {
    return (arr[2]);
  } else {
    return null;
  }
}

Vue.prototype.getCookie = getCookie;

// cookie
Vue.prototype.delCookie = (name) => {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval = getCookie(name);
  if (cval != null) {
    document.cookie = name + '=' + cval + ';expires=' + exp.toGMTString();
  }
};

// vue
var app = new Vue({
  data: {},
  el: '#app',
  render: h => h(App),
  router,
  store,
  watch: {
    '$route': 'checkLogin'
  },
  created() {
    this.checkLogin();
  },
  methods: {
    checkLogin() {
      // session
      if (!this.getCookie('session')) {
        this.$router.push('/login');
      }
      // else {
      //   this.$router.push('/schoolInfo');
      // }
    }
  }
});



