<template>
  <div id="app">
    <transition name="fade">
      <keep-alive>
        <router-view :key="key"></router-view>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        isLogin: false,
        userInfo: { // 
          nick: null,
          ulevel: null,
          uid: null,
          portrait: null
        }
      }
    },
    mounted () {
      // 
      this.getUserInfo();
    },
    computed: {
      key() {
        return this.$route.name !== undefined? this.$route.name + +new Date(): this.$route + +new Date()
      }
    },
    methods: {
      // 
      getUserInfo () {
        // http
        this.userInfo = {
          nick: 'Doterlin',
          ulevel: 20,
          uid: '10000'
        };

        // mutationStore
        this.$store.commit('updateUserInfo', this.userInfo);
      }
    }
  };
</script>

<style>
  #app {
    width: 100%;
    background-color: #fff;
  }
</style>



